@media all and (max-width: 1390px) {
	.select_category {
		.categories_block {
			width: 100%;
			padding: 0 10px; } } }

@media all and (max-width: 1200px) {
	.hide-1 {
		display: none; }
	.select_category {
		margin-top: -35px; }
	.header_box_for_buttons {
		display: block;
		margin: auto;
		width: 100%;
		text-align: center; }
	.header-body {
		form.search {
			width: 58%;
			transition: 0.4s; } }
	.header_box_for_buttons {
		.header_big_button {
			padding: 15px 40px; }
		.header_big_button:last-child {
			padding: 0; }
		.account_dropdown {
			a {
				padding: 15px 40px; } } } }



@media all and (max-width: 1024px) {
	.hide-2 {
		display: none; }
	.show-2 {
		display: inline; } }


@media all and (max-width: 992px) {
	footer {
		.logo_footer {
			display: none; }
		.menu_holder {
			text-align: center;
			width: 100%;
			margin-bottom: 30px; }
		.copyright {
			text-align: center;
			margin-left: 0; } }
	.left_side_bar {
		display: none; } }

@media all and (max-width: 820px) {
	header {
		.menu {
			ul {
				padding: 0 10px; } } } }



@media (min-width: 780px) {
	.navbar-toggle {
 } }		// display: none


@media all and (min-width: 767px) {
	.language_dropdown {
		float: right; }
	.navbar-toggle {
		float: left; }
	header {
		.menu {
			position: absolute;
			background: white;
			z-index: 150;
			width: 100%;
			top: 50px;
			padding: 0px 15px;
			ul {
				margin-left: 0;
				margin-right: 0;
				margin-top: 0;
				li {
					display: block;
					text-align: left; } } } } }

@media all and (min-width: 768px) {
	.navbar-collapse.collapse {
		position: relative;
		top: 0;
		width: 85.2%;
		display: inline-block!important;
		height: auto!important;
		padding-bottom: 0;
		overflow: visible!important; }
	header {
		.menu {
			ul {
				li {
					display: inline-block;
					text-align: center; } } } } }

@media all and (max-width: 767px) {
	header {
		.menu {
			position: absolute;
			background: white;
			z-index: 150;
			width: 100%;
			top: 50px;
			padding: 0px 15px;
			// height: 1px
			ul {
				li {
					display: block;
					text-align: left; } } } }
	main {
		.jumbo {
			.static-block {
				display: none; }
			.slider-jumbo {
				width: 100%; } } }
	.navbar-toggle {
		float: left; }
	.language_dropdown {
		float: right; }
	.header_box_for_buttons {
		text-align: right; }

	.navbar-nav {
		margin-left: 0;
		margin-top: 0; }
	header .menu ul li:first-child a {
		margin-left: 0; }
	.horizontal_items {
		.hide_col {
			display: none; }
		.category_name,
		.price_holder,
		.short_product_name {
			display: block; }
		.category_name {
			text-align: left;
			display: block;
			margin-bottom: 10px; }

		.price_holder {
			position: static;
			text-align: left;
			margin-top: 12px; }
		.feedback,
		.orders {
			display: inline-block;
			margin-top: 10px; }
		.feedback {
			margin-right: 30px; }
		.short_product_name {
			color: #222;
			font-size: 15px;
			font-weight: 600; } } }


@media all and (max-width: 665px) {
	.language_dropdown {
		.arrow-down {
			display: none; } } }


@media all and (max-width: 610px) {
	.header_box_for_buttons {
		text-align: center; }
	.select_category {
		margin: 15px 0; }
	.header_box_for_buttons {
		.header_big_button {
			padding: 15px 30px; }
		.header_big_button:last-child {
			padding: 0; }
		.account_dropdown {
			a {
				padding: 15px 30px; } } } }


@media all and (max-width: 586px) {
	footer {
		.menu_footer {
			width: 45%;
			margin-bottom: 30px; } } }




@media all and (max-width: 580px) {
	.item {
		width: 100%; } }

@media all and (max-width: 560px) {
	.horizontal_items {
		.category_name {
			text-align: center; }

		.about_col {
			margin-top: 15px; }
		.hide_now {
			display: none; }
		.hide_col,
		.about_col,
		.pr_pic_col {
			display: block;
			width: 100%;
			text-align: center; }
		.product_name {
			padding: 0 30px;
			text-align: center;
			max-height: 80px; }

		.price_holder {
			position: static;
			margin-top: 0;
			text-align: center; }
		.subcol_content {
			height: auto; }
		.h_item {
			position: relative; }
		.about_col {
			position: static; }
		.item_rating {
			position: absolute;
			top: 15px;
			left: 40px; }
		.like_ikon {
			top: 20px;
			right: 40px; }
		.shipping {
			display: inline-block;
			margin-right: 30px; } } }





@media all and (max-width: 475px) {
	form.bussiness {
		.form_row {
			width: 100%; } }
	button.check {
		margin: 10px 15px; } }



@media all and (max-width: 426px) {
	.header-body {
		.logo {
			width: 35px;
			height: 39px; }
		form.search {
			margin-top: 15px;
			margin-bottom: 10px;
			input[type="search"] {
				height: 35px;
				padding: 5px 10px; }
			.search_button {
				height: 35px; } } }
	.header_box_for_buttons {
		.header_big_button {
			padding: 15px 15px; }
		.header_big_button:last-child {
			padding: 0; }
		.account_dropdown {
			a {
				padding: 15px 15px; } } }
	.banner {
		.banner_title {
			font-size: 40px; } }
	.header-body {
		form.search {
			.search_button {
				font-size: 13px; } } }
	.header_box_for_buttons {
		.header_big_button {
			span {
				font-size: 12px; } } }
	main {
		h2.categories_title {
			font-size: 20px; } }
	main {
		.jumbo {
			.static-block {
				display: none; }
			.slider-jumbo {
				width: 100%; } } }
	.slick-dots {
		li {
			button {
				height: 10px;
				width: 10px; } } }
	main {
		.categories {
			.carusel_item_title {
				font-size: 14px; } } }
	main {
		.categories {
			.slick-prev {
				left: 20px; } } }
	main {
		.categories {
			.slick-next {
				right: 20px; } } } }



